.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #61ace9;
}

.header {
  padding: 2rem 0 0 0;
}
@media (min-width: 768px) {
  .header__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
    align-items: center;
  }
}
.header__logo {
  margin: 0 auto;
  background-image: url(../img/logo.png);
  background-repeat: no-repeat;
  background-size: cover;
  width: 15rem;
  height: 15rem;
  filter: brightness(1.1);
  mix-blend-mode: multiply;
}
@media (min-width: 768px) {
  .header__logo {
    margin: 0;
    width: 25rem;
    height: 25rem;
  }
}
.header__redes-sociales {
  text-align: center;
}
.header__enlaces {
  padding-top: 1rem;
}
.header__enlace {
  width: 5rem;
  height: 5rem;
  padding-left: 1rem;
}
@media (min-width: 768px) {
  .header__enlace {
    width: 8rem;
    height: 8rem;
  }
}

.nav {
  padding: 1rem 0;
  background-color: #61ace9;
}
.nav__grid {
  border: 2px dashed #fff;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .nav__grid {
    flex-direction: row;
    justify-content: space-around;
  }
}
.nav__enlace {
  color: #fff;
  font-size: 2.4rem;
  margin-bottom: 1rem;
  width: 100%;
  text-align: center;
  padding: 1rem;
}
.nav__enlace:last-child {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .nav__enlace {
    margin-left: 1rem;
  }
}
.nav__enlace:hover {
  background-color: #316ea0;
  padding: 1rem;
}

.populares {
  background-color: #eceeef;
  overflow: hidden;
}
.populares__heading {
  text-align: center;
}
.populares__heading span {
  font-family: "Share Tech", sans-serif;
  font-weight: 400;
  color: #316ea0;
  font-size: 4.6rem;
}
.populares__cursos {
  overflow: hidden;
  width: 30rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .populares__cursos {
    width: 50rem;
  }
}
.populares__curso {
  height: 100%;
  flex-shrink: 0;
  width: 100%;
  position: relative;
  transition-property: transform;
}
.populares .swiper-pagination.swiper-pagination-bullets.swiper-pagination-horizontal {
  margin-bottom: -20rem;
}
@media (min-width: 768px) {
  .populares .swiper-pagination.swiper-pagination-bullets.swiper-pagination-horizontal {
    margin-bottom: -5rem;
  }
}
@media (min-width: 1024px) {
  .populares .swiper-pagination.swiper-pagination-bullets.swiper-pagination-horizontal {
    margin-bottom: -35rem;
  }
}
.populares .swiper-pagination-bullet {
  background-color: black;
}
.populares .swiper-pagination-bullet-active {
  background-color: #000;
}
.populares .swiper-button-next,
.populares .swiper-button-prev {
  color: #316ea0;
  margin-top: 450px;
}
@media (min-width: 768px) {
  .populares .swiper-button-next,
  .populares .swiper-button-prev {
    margin-top: 400px;
  }
}

.ofertas__heading {
  text-align: center;
  padding-top: 3rem;
  margin-bottom: 1rem;
}
.ofertas__heading span {
  font-family: "Share Tech", sans-serif;
  font-weight: 400;
  color: #316ea0;
  font-size: 4.8rem;
}
.ofertas__contenido {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  column-gap: 3rem;
}
@media (min-width: 768px) {
  .ofertas__contenido {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 3rem;
  }
}
@media (min-width: 1024px) {
  .ofertas__contenido {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 3rem;
  }
}
.ofertas__grid {
  background-color: #eceeef;
  border-radius: 1rem;
  margin-bottom: 5rem;
  transition: transform 0.3s;
  border: 2px solid #e6e6e6;
}
.ofertas__grid:hover {
  background-color: #d0d5d8;
  border: 2px solid #cccccc;
  transform: scale(1.1);
}

.info {
  margin-bottom: 5rem;
  padding-top: 10rem;
  padding-left: 10rem;
  border-top: 2px solid #eceeef;
}
@media (min-width: 768px) {
  .info {
    padding-left: 0;
  }
}
.info__lenguaje {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.info__curso {
  line-height: 1;
  width: 15rem;
}
.info__materia {
  width: 10rem;
  height: 10rem;
}
.info__instructor {
  background-image: url(../img/maestro.jpg);
  background-size: cover;
  background-position: center;
  width: 8rem;
  height: 8rem;
  border-radius: 50%;
}
.info__instructor--instructor2 {
  background-image: unset;
  background-image: url(../img/maestro2.jpg);
  background-size: cover;
  background-position: center right;
}
.info__instructor--instructor3 {
  background-image: unset;
  background-image: url(../img/maestro3.jpeg);
  background-size: cover;
  background-position: center;
}
.info__informacion {
  display: flex;
  align-items: center;
}
.info__texto {
  margin-left: 2rem;
}
.info__profesor, .info__costo, .info__duracion {
  margin: 0;
}

.beneficios__heading {
  text-align: center;
}
.beneficios__heading span {
  font-family: "Share Tech", sans-serif;
  color: #316ea0;
}
.beneficios__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  column-gap: 2rem;
}
@media (min-width: 768px) {
  .beneficios__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 4rem;
  }
}

.beneficio {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.beneficio__imagen {
  width: 12rem;
  height: 12rem;
}
.beneficio__texto {
  margin: 0;
  font-size: 2.2rem;
  font-weight: 700;
  color: #61ace9;
}
.beneficio__lorem {
  font-size: 1.6rem;
}

.testimoniales {
  padding: 5rem 0;
  background-color: #61ace9;
}
.testimoniales__heading {
  text-align: center;
  font-family: "Share Tech", sans-serif;
  color: #316ea0;
  font-size: 4.2rem;
}

.testimonial__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  column-gap: 2rem;
  gap: 2rem;
}
@media (min-width: 768px) {
  .testimonial__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 4rem;
    align-items: center;
  }
}
.testimonial__contenido {
  background-color: #fff;
  padding: 1rem 0 1rem 5rem;
  border-radius: 1rem;
}
.testimonial__texto {
  position: relative;
}
.testimonial__texto::before {
  content: " '' ";
  display: block;
  position: absolute;
  top: 0rem;
  left: -2rem;
  font-size: 4.2rem;
  font-weight: 900;
}
.testimonial__texto::after {
  content: " '' ";
  display: block;
  position: absolute;
  top: 0rem;
  left: 19rem;
  font-size: 4.6rem;
  font-weight: 900;
}
.testimonial__autor {
  text-align: right;
  padding-right: 1rem;
  color: #316ea0;
  font-family: "Share Tech", sans-serif;
  font-weight: 700;
}

.footer {
  background-image: url(../img/tecnologia.jpg);
  background-size: cover;
  padding: 5rem 0;
  position: relative;
}
.footer::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.footer__grid {
  text-align: center;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 3rem;
  }
}
.footer__titulo {
  color: #fff;
  font-size: 2.8rem;
  text-transform: uppercase;
  font-weight: 900;
}
.footer__historia, .footer__mision, .footer__politica, .footer__terminos, .footer__ayuda, .footer__confianza {
  color: #fff;
  text-transform: uppercase;
}
.footer__copyright {
  color: #fff;
  margin-top: 5rem;
  text-align: center;
  font-weight: 700;
}

.quienes-somos {
  padding: 5rem 0;
}
.quienes-somos__heading {
  text-align: center;
  font-family: "Share Tech", sans-serif;
  color: #316ea0;
  font-size: 3.8rem;
}
@media (min-width: 768px) {
  .quienes-somos__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
    align-items: center;
  }
}
.quienes-somos__imagen {
  width: 100%;
  margin-bottom: 5rem;
}
.quienes-somos__imagen:nth-child(3) {
  margin-bottom: 0;
}
.quienes-somos__texto {
  text-align: center;
  height: 100%;
}

.formulario {
  padding: 5rem 0;
  background-color: #d1e7f9;
}
.formulario__heading {
  text-align: center;
  font-size: 3.6rem;
  color: #61ace9;
}
.formulario__heading span {
  font-family: "Share Tech", sans-serif;
  color: #316ea0;
}
.formulario__text {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 40rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .formulario__text {
    max-width: 60rem;
  }
}
.formulario__text input[type=text] {
  margin: 1rem;
  padding: 1rem;
  width: 40rem;
  border: 2px solid #61ace9;
}
@media (min-width: 768px) {
  .formulario__text input[type=text] {
    width: 50rem;
  }
}
.formulario__nombre, .formulario__asunto, .formulario__mensaje {
  display: flex;
  align-items: center;
  font-weight: 900;
}
.formulario__label {
  width: 8rem;
  font-size: 1.8rem;
  text-transform: uppercase;
  color: #316ea0;
}
.formulario__label--textarea {
  display: block;
  margin-bottom: 15rem;
}
.formulario__input {
  box-shadow: 0px 0px 0px 2px rgba(49, 110, 160, 0.15);
}
.formulario__input:focus-visible {
  outline: 2px solid #61ace9;
  border: none;
}
.formulario__input::placeholder {
  font-size: 1.8rem;
  font-weight: 700;
  color: #7aadd6;
}
.formulario__textarea {
  box-shadow: 0px 0px 0px 2px rgba(49, 110, 160, 0.15);
  margin: 1rem 2rem;
  padding: 1rem;
  resize: none;
  width: 40rem;
  height: 20rem;
  border: 2px solid #61ace9;
}
@media (min-width: 768px) {
  .formulario__textarea {
    width: 50rem;
  }
}
.formulario__textarea:focus-visible {
  outline: 2px solid #61ace9;
  border: none;
}
.formulario input[type=submit] {
  width: 20rem;
  padding: 2rem 0;
  background-color: #61ace9;
  border: none;
  color: #fff;
  cursor: pointer;
  font-weight: 700;
  text-transform: uppercase;
}
.formulario input[type=submit]:hover {
  background-color: #316ea0;
}