@use 'variables' as v;

html{
    font-size: 62.5%;
    box-sizing: border-box;
}

*,*::before,*::after{
    box-sizing: inherit;
}

body{
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
}

p{
    color: v.$negro;
    font-size: 2rem;
}

.contenedor{
    max-width: 120rem;
    width: 90%;
    margin: 0 auto;
}

a{
    text-decoration: none;
}

img{
    max-width: 100%;
    display: block;
}

h1,h2,h3{
    margin: 0 0 v.$separacion 0;
}

h1{
    font-size: 4rem;
}

h2{
    font-size: 3.6rem;
}

h3{
    font-size: 3.2rem;
}

li{
    list-style: none;
    display: inline-block;
}